// http://www.dropzonejs.com/#event-list
"use strict";
exports.DropzoneEvents = [
    'drop',
    'dragstart',
    'dragend',
    'dragenter',
    'dragover',
    'dragleave',
    'addedfile',
    'removedfile',
    'thumbnail',
    'error',
    'processing',
    'uploadprogress',
    'sending',
    'success',
    'complete',
    'canceled',
    'maxfilesreached',
    'maxfilesexceeded',
    'processingmultiple',
    'sendingmultiple',
    'successmultiple',
    'completemultiple',
    'canceledmultiple',
    'totaluploadprogress',
    'reset',
    'queuecomplete'
];
class DropzoneConfig {
    constructor(config = {}) {
        this.assign(config);
    }
    assign(config = {}) {
        for (let key in config) {
            this[key] = config[key];
        }
        if (this.server) {
            this.url = this.server + (this.params ? ('?' + this.params) : '');
        }
    }
}
exports.DropzoneConfig = DropzoneConfig;



